.edgtf-header-vertical {

    .edgtf-vertical-area-widget-holder {
        @include edgtfRelativeHolderLayout();
        margin: 10px 0 0;
        z-index: 100;
    }

    .edgtf-vertical-alignment-center{

        .edgtf-vertical-area-widget-holder {
            position: absolute;
            width: 100%;
            bottom: 0px;
            left: 0px;
        }
    }
}