/* ==========================================================================
   Header Vertical navigation style - begin
   ========================================================================== */

.edgtf-header-vertical {

    .edgtf-vertical-alignment-center {
    
        .edgtf-vertical-menu-outer {
            @include edgtfAbsoluteHolderLayout();
            padding: 100px 0;
        }
    
        .edgtf-vertical-menu {
            display: table;
            width: 100%;
            height: 100%;
            min-height: 0;
        
            > ul {
                display: table-cell;
                width: 100%;
                height: 100%;
                vertical-align: middle;
            }
        }
    }
    
    .edgtf-vertical-menu-outer {
        @include edgtfRelativeHolderLayout();
	    box-sizing: border-box;
    }

    .edgtf-vertical-menu {
        @include edgtfRelativeHolderLayout();
        margin: 58px 0 31px;
        padding: 0;
        z-index: 100;
        
        ul {
            position: relative;
            list-style-type: none;
            padding: 0;
            margin: 0;
            
            li {
                position: relative;
                padding: 0;
                margin: 0;
                
                a {
	                position: relative;
	                display: block;
	                margin: 0;
	                padding: 5px 0;
                    font-size: 16px;
                    line-height: 24px;
                    font-style: italic;
                    color: $first-main-color;
                    font-family: $default-heading-font;
                    
                    &:hover {
                        color: $first-main-color;
                        
                        .edgtf-menu-arrow {
                            @include edgtfTransform(rotate(0deg));
                        }
                    }
                    
                    .item_outer {
                        position: relative;
                        display: block;
                        padding: 0 15px 0 0;
                        box-sizing: border-box;
                    }
                    
                    .edgtf-menu-arrow {
                        position: absolute;
                        top: 1px;
                        right: 0;
	                    font-size: inherit;
	                    line-height: inherit;
                        margin: 0;
                        @include edgtfTransform(rotate(-90deg));
                        @include edgtfTransitionTransform(.2s ease-in-out);
                    }
                    
                    .edgtf-menu-featured-icon {
                        position: relative;
                        top: -4px;
                        left: 3px;
                        color: $default-heading-color;
                        font-size: 12px;
                        margin: 0;
                    }
                    
                    .menu_icon:not(.blank):not(.null) {
                        padding: 0 5px 0 0;
                    }
                }
	
	            &.edgtf-active-item > a,
	            &.current_page_item > a,
	            &.current-menu-item > a,
	            &.current-menu-ancestor > a {
		            color: $first-main-color;
                    
                    .edgtf-menu-arrow {
                        @include edgtfTransform(rotate(0deg));
                    }
                }
                
                ul {
                    padding: 3px 0;
                    
                    li {
                        
                        &.sub {
                            
                            > a {
                                
                                .item_outer {
                                    position: relative;
                                    display: block;
                                    
                                    &:after {
                                        position: absolute;
                                        top: -1px;
                                        right: 0;
                                        margin: 0;
                                        font-family: 'FontAwesome';
	                                    font-size: inherit;
	                                    line-height: inherit;
                                        content: "\f105";
                                    }
                                }
                            }
                            
                            &.current_page_item > a,
                            &.current-menu-ancestor > a {
                                
                                .item_outer:after {
                                    content: "\f107";
                                }
                            }
                        }
                    }
                }
                
                .second {
                    display: none;
                    @include edgtfTransform(translateZ(0));
                    
                    .inner {
                        position: relative;
                        display: block;
                        padding: 0;
                        z-index: 997;
                    }
                }
            }
        }
        
        > ul {
            
            > li {
                
                > a {
                    font-size: 16px;
                    line-height: 30px;
                    font-style: normal;
                    
                    span.item_text {
	                    position: relative;
	                    display: inline-block;
	                    vertical-align: top;
                    }
                }
            }
        }
    }
}

.edgtf-light-header.edgtf-header-vertical .edgtf-vertical-menu ul li {
    
    a {
        color: $header-light-color !important;
        
        &:hover {
            color: $header-light-hover-color !important;
        }
    }
    
    ul li {
        
        &.current_page_item > a,
        &.current-menu-item > a,
        &.current-menu-ancestor > a {
            color: $header-light-hover-color !important;
        }
    }
}

.edgtf-light-header.edgtf-header-vertical .edgtf-vertical-menu > ul > li {
    
    &.edgtf-active-item > a,
    &.current-menu-ancestor > a {
        color: $header-light-hover-color !important;
    }
}

.edgtf-dark-header.edgtf-header-vertical .edgtf-vertical-menu ul li {
    
    a {
        color: $header-dark-color !important;
        
        &:hover {
            color: $header-dark-hover-color !important;
        }
    }
    
    ul li {
        
        &.current_page_item > a,
        &.current-menu-item > a,
        &.current-menu-ancestor > a {
            color: $header-dark-hover-color !important;
        }
    }
}

.edgtf-dark-header.edgtf-header-vertical .edgtf-vertical-menu > ul > li {
    
    &.edgtf-active-item > a,
    &.current-menu-ancestor > a {
        color: $header-dark-hover-color !important;
    }
}
/* ==========================================================================
   Header Vertical navigation style - end
   ========================================================================== */